const Category = {
    OFF_BODY: 'Off-body',
    COMBO: 'Combo',
    SPLEIS: 'Spleiselag'
};

const triks = [
    {
        "id": 0,
        "name": "Spleiselag: Ylva S",
        "description": "Start med forlengs weave. S sidelengs weave. Gjr en isopop (ring ut, mens armen gr opp). " +
        "S en reversert jump-through (snu deg rundt og hopp igjennom ringen). Gjr en hndvri til bakoverviv. " +
        "S en hndvri til foroverviv mens du snur deg rundt 180 grader",
        "categories": [
            Category.OFF_BODY,
            Category.COMBO,
            Category.SPLEIS
        ],
        "leadingTricks": [],
        "trailingTricks": [],
        "video": "https://www.facebook.com/ylva.s.3/videos/10159799879310253/"
    },{
        "id": 1,
        "name": "Spleiselag: Tina G",
        "description": "Gjr en foroverviv med hndspinn p hyre hnd. Snu deg til siden og bytt grep som om du " +
        "skal gjre en bakoverviv, men gjr en hndspin foran deg. Bytt til venstre hnd, kast ring bak og over " +
        "skulderen og ta i mot med venstre hnd. Fr ringen foran deg til hyre hnd. Gjr en utoverisalsjon, bytt " +
        "deretter til innover isolasjon. Tilslutt gjr du en coin flip (vri ring helt rundt bakover, snu deg 180 " +
        "grader, sl ring tilbake)",
        "categories": [
            Category.OFF_BODY,
            Category.COMBO,
            Category.SPLEIS
        ],
        "leadingTricks": [],
        "trailingTricks": [],
        "video": "https://www.facebook.com/thetinagreen/videos/10155173501266592/"
    }, {
        "id": 2,
        "name": "Spleiselag: Christina S",
        "description": "Flipp ringen over ryggen: Start med tommel opp p ringen, rull over hnda og fr ringen " +
        "rundt foran deg slik at tommelen kommer opp. Fr ringen bak ryggen og la den falle ned(som fra escalator), " +
        "sett ben i kryss, fr ring ut og rundt mens du snur deg helt rundt. " +
        "Bytt hnd og gjr isopops til hver side.",
        "categories": [
            Category.OFF_BODY,
            Category.COMBO,
            Category.SPLEIS
        ],
        "leadingTricks": [],
        "trailingTricks": [],
        "video": "https://www.facebook.com/gerald.torgersen/videos/10215738877330922/"
    }, {
        "id": 3,
        "name": "Spleiselag: Kristina S",
        "description": "Snurr ringen forover p hnda. Stikk albuen inn i stedet, med albue pekende bakover. Stikk" +
        "den andre albuen inn of fortsett snurringa p andre hnd.",
        "categories": [
            Category.OFF_BODY,
            Category.SPLEIS
        ],
        "leadingTricks": [],
        "trailingTricks": [],
        "video": "https://www.facebook.com/kristina.lakic/videos/10155796624011014/"
    }

];


export const count = () => {
    return triks.length;
};

export const allTricks = () => {
    return triks;
};

export const get = (id) => {
    const isTriks = t => t.id === id
    return triks.find(isTriks)
};





